

import {login, register} from '@/api'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
components: {
XHeader,
Icon
},
name: 'login',
data () {
return {
userName: '',
password: '',
newName: '',
newPassword1: '',
newPassword2: '',
showLogin: true
}
},
methods: {
backspace () {
this.$router.go(-1)
},
userLogin () {
if (this.userName === '' || this.password === '') {
this.$message({
message: '请输入用户名或密码',
type: 'warning',
center: true
})
} else {
var userVo = {
'name': this.userName,
'password': this.password
}
/* 接口请求 */
login(userVo).then((response) => {
/*  接口的传值是(-1,该用户不存在),(-2,密码输入错误),(1,登录成功)  */
if (response.data.code === -1) {
this.$message({
message: '该用户不存在!',
type: 'error',
center: true
})
} else if (response.data.code === -2) {
this.$message({
message: '密码输入错误!',
type: 'error',
center: true
})
} else {
this.$message({
message: '登录成功!',
type: 'success',
center: true
})
localStorage.setItem('userName', this.userName)
this.$store.commit('setUserData', response.data.data)
setTimeout(function () {
this.$router.push({path: '/'})
this.$store.dispatch('login')
}.bind(this), 1000)
this.userName = ''
this.password = ''
}
})
}
},
UserRegister () {
if (this.newName === '' || this.newPassword1 === '') {
this.$message({
message: '请输入用户名或密码',
type: 'warning',
center: true
})
} else if (this.newPassword2 === '') {
this.$message({
message: '请再次输入密码',
type: 'warning',
center: true
})
} else if (this.newPassword1 !== this.newPassword2) {
this.$message({
message: '请核对两次新密码',
type: 'erorr',
center: true
})
} else {
let data = {
'name': this.newName,
'password': this.newPassword1
}
register(data).then((response) => {
if (response.data.message === 'ok') {
this.$message({
message: '注册成功,请登录',
type: 'success',
center: true
})
this.newName = ''
this.newPassword1 = ''
this.newPassword2 = ''
/* 注册成功之后再跳回登录页 */
setTimeout(function () {
this.showLogin = true
}.bind(this), 1000)
}
})
}
}
}
}



