
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {getTabAllMessage, getAllMessage, getUser, addPraise, addView, addCollection, search} from '@/api'
import {split} from '@/util/splitArray'

export default {
components: {
Cell,
Group,
XHeader,
XButton,
Badge,
Tab,
TabItem
},
name: 'home',
data () {
return {
sliderIndex: Number,
line: 'v',
line_gap: 100,
conheight: {
height: '',
background: '#c8e4da'
},
option: {
effect: 'nest',
currentPage: 1,
direction: 'horizontal',
pagination: true,
thresholdDistance: 50,
thresholdTime: 300,
loop: true
},
messageArrayList: [],
messageTabs: '',
searchWord: '',
cardStyle: {
width: '100%',
height: 'auto',
background: '#8bceb6',
padding: '0px'
}
}
},
created () {
this.getDatas()
window.addEventListener('resize', this.getHeight)
this.getHeight()
window.addEventListener('resize', this.getWidth)
this.getWidth()
this.sliderIndex = this.$store.getters.getCurrent
this.$store.dispatch('hideSideBar')
},
destroyed () {
this.$store.commit('setCurrent', this.sliderIndex)
},
computed: {
isShowside () {
return this.$store.getters.isShowside
},
showUserIcon () {
return this.$store.getters.isShowUserIcon
},
messageList () {
return this.$store.getters.getMessageList
},
user () {
return this.$store.getters.getUserData
}
},
mounted () {
/* 页面挂载获取cookie，如果存在username的cookie，则跳转到主页，不需登录 */
if (localStorage.hasOwnProperty('userName')) {
this.$router.push({path: '/'})
this.$store.dispatch('login')
var user = {}
user.userName = localStorage.getItem('userName')
getUser(user).then((response) => {
this.$store.commit('setUserData', response.data.data)
})
}
},
methods: {
getDatas () {
getAllMessage().then((response) => {
var messageList = response.data.data
for (var i in messageList) {
messageList[i].imageHeight = messageList[i].imageHeight * 0.55 + 120
messageList[i].isCollection = false
}
this.$store.commit('setMessageList', messageList)
this.messageArrayList = split(messageList, 4)
})
},
getData (datas) {
var messageVO = {}
this.messageArrayList = []
messageVO.messageTabs = datas
getTabAllMessage(messageVO).then((response) => {
var messageList = response.data.data
for (var i in messageList) {
messageList[i].imageHeight = messageList[i].imageHeight * 0.55 + 120
messageList[i].isCollection = false
}
this.$store.commit('setMessageList', messageList)
this.messageArrayList = split(messageList, 4)
})
},
login () {
this.$router.push({path: '/login'})
},
addMessage () {
this.$router.push({path: '/add'})
},
showUserDetail (item) {
if (item.userId === this.$store.getters.getUserData.id) {
this.showUserSelf()
} else {
var user = {}
user.userId = item.userId
user.userName = item.userName
user.headImg = item.headImg
this.$store.commit('setCurrentUser', user)
this.$router.push({path: '/userDetail'})
}
},
showUserSelf () {
this.$router.push({path: '/userSelf'})
},
toPersonalCollection () {
this.$router.push({path: '/personalCollection'})
},
showMessageDetail (item) {
item.pageViews++
var messageVO = {}
messageVO.messageId = item.messageId
addView(messageVO)
this.$store.commit('setCurrentMessage', item)
this.$router.push({path: '/messageDetail'})
},
showCarePerson () {
this.$router.push({path: '/care'})
},
addPraisePoint (item) {
item.praisePoint++
var messageVO = {}
messageVO.messageId = item.messageId
addPraise(messageVO)
},
addCollectionNum (item) {
if (localStorage.hasOwnProperty('userName')) {
var messageVO = {}
messageVO.userId = this.$store.getters.getUserData.id
messageVO.messageId = item.messageId
addCollection(messageVO).then((response) => {
if (response.data.code === 1) {
item.collectionNumber++
this.$message({
message: '收藏成功!',
type: 'success',
center: true
})
} else if (response.data.code === -1) {
this.$message({
message: '您已收藏留言!',
type: 'error',
center: true
})
} else {
this.$message({
message: '收藏失败!',
type: 'error',
center: true
})
}
})
} else {
this.$message({
message: '请先登录!',
type: 'success',
center: true
})
this.$router.push({path: '/login'})
}
},
getHeight () {
this.conheight.height = window.innerHeight - 8 + 'px'
},
getWidth () {
this.line_gap = window.innerWidth / 2 - 2
},
slideNext () {
this.$refs.slider.next()
},
slidePre () {
this.$refs.slider.prev()
},
showSide () {
this.$store.dispatch('showSideBar')
},
hideSide () {
this.$store.dispatch('hideSideBar')
},
logout () {
this.hideSide()
this.$store.dispatch('logout')
/* 删除cookie */
localStorage.clear()
},
toManagePage () {
this.$router.replace({path: '/admin'})
},
selectMenu (item) {
if (item === '精选') {
this.getDatas()
} else {
this.getData(item)
}
},
search () {
var messageVO = {}
this.messageArrayList = []
messageVO.messageContent = this.searchWord
search(messageVO).then((response) => {
var messageList = response.data.data
for (var i in messageList) {
messageList[i].imageHeight = messageList[i].imageHeight * 0.55 + 135
messageList[i].isPraise = false
messageList[i].isCollection = false
}
this.$store.commit('setMessageList', messageList)
this.messageArrayList = split(messageList, 4)
})
},
getRandomColor () {
var rand = Math.floor(Math.random() * 0xFFFFFF).toString(16)
if (rand.length === 6) {
return rand
} else {
return this.getRandomColor()
}
}
}
}



